import './Styles/Footer.css';
import { useState, useEffect } from 'react'
import { useForm } from 'react-hook-form';
import logo from '../assets/logo-bookmark-white.svg'
import { scroller } from 'react-scroll';

export default function Footer() {
  const [joinedAmount, setJoinedAmount] = useState(35000)
  const [sendEmail, setSendEmail] = useState(false);

  let messageTimeout;

  const handleSendClick = () => {
    if (messageTimeout) {
      clearTimeout(messageTimeout);
    }
    setSendEmail(true);
    messageTimeout = setTimeout(() => {
      setSendEmail(false);
    }, 2000);
  };

  //useForm to validate email when form submitted
  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm();
  const onSubmit = (values) => handleSendClick()


  //reset joined number in 20s
  const totalDuration = 20000; 
  useEffect(() => {
    let startTime;
    function animate(time) {
      if (!startTime) startTime = time;
      const progress = time - startTime;
      const percentage = Math.min(1, progress / totalDuration);
      setJoinedAmount(Math.round(percentage * 35000));

      if (percentage < 1) {
        requestAnimationFrame(animate);
      }
    }

    const animationId = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(animationId);
  }, [totalDuration]);

  //adds dot on number
  function formattedJoinedAmount(number) {
    if (!number) {
      return 0
    }
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  }

  const scrollToSection = (section) => {
    scroller.scrollTo(section, {
      duration: 800,
      delay: 0,
      smooth: 'easeInOutQuart',
    });
  };

  return (
    <div className='footer--container'>
      {sendEmail && <div className='message-container'>
          <span className="message">We have recieved your email!</span>
        </div>}
      <div className='footer--top'>
        <div className='footer--joined'>
          <div className='footer-numbers'>{formattedJoinedAmount(joinedAmount)}</div>
          <div>+ ALREADY JOINED</div>
        </div>
        <h2>Stay up-to-date with what we're doing</h2>
        <form onSubmit={handleSubmit(onSubmit)}>
          <div className='footer--email'>
              <div className={errors.email ? 'footer--warning' : ''}>
                  <input 
                    className={errors.email? 'footer--input2' : 'footer--input'} maxLength={30}
                    type="text"
                    placeholder='Enter your email address'
                    {...register("email", { required: true, pattern: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i })}
                  />
                {errors.email && <div>Whoops, make sure it's an email</div>}
              </div>
              <button type='submit' className='footer--button'>Contact Us</button>
          </div>
        </form>
      </div>
      <div className='footer--bottom'>
        <div className='bottom--left'>
          <img src={logo} alt='logo' onClick={() => scrollToSection('header')}/>
          <div className='footer--buttons'>
            <button className='button--footer'
            onClick={() => scrollToSection('features')}>FEATURES</button>
            <button className='button--footer'
            onClick={() => scrollToSection('download')}>DOWNLOD</button>
            <button className='button--footer'
            onClick={() => scrollToSection('faq')}>FAQ</button>
          </div>
        </div>
        <div className='footer--media'>
           <svg className="social--svg" xmlns="http://www.w3.org/2000/svg" width="24" height="24"><path fillRule="evenodd" d="M22.675 0H1.325C.593 0 0 .593 0 1.325v21.351C0 23.407.593 24 1.325 24H12.82v-9.294H9.692v-3.622h3.128V8.413c0-3.1 1.893-4.788 4.659-4.788 1.325 0 2.463.099 2.795.143v3.24l-1.918.001c-1.504 0-1.795.715-1.795 1.763v2.313h3.587l-.467 3.622h-3.12V24h6.116c.73 0 1.323-.593 1.323-1.325V1.325C24 .593 23.407 0 22.675 0z"/></svg>
           <svg className="social--svg" xmlns="http://www.w3.org/2000/svg" width="24" height="20"><path fillRule="evenodd" d="M24 2.557a9.83 9.83 0 0 1-2.828.775A4.932 4.932 0 0 0 23.337.608a9.864 9.864 0 0 1-3.127 1.195A4.916 4.916 0 0 0 16.616.248c-3.179 0-5.515 2.966-4.797 6.045A13.978 13.978 0 0 1 1.671 1.149a4.93 4.93 0 0 0 1.523 6.574 4.903 4.903 0 0 1-2.229-.616c-.054 2.281 1.581 4.415 3.949 4.89a4.935 4.935 0 0 1-2.224.084 4.928 4.928 0 0 0 4.6 3.419A9.9 9.9 0 0 1 0 17.54a13.94 13.94 0 0 0 7.548 2.212c9.142 0 14.307-7.721 13.995-14.646A10.025 10.025 0 0 0 24 2.557z"/></svg>
        </div>
      </div>
    </div>
    )
}
